$width: 60px;

.search-result {
  display: flex;
  place-content: center space-between;
}

.search-result__img {
  width: $width;
  height: $width;
  object-fit: cover;
}

.search-result__content {
  flex: 1;
  padding: 0 0 0 10px;
}
